import Vue from 'vue'
import Vuex from 'vuex'
import { GET_GOODS } from '../../api/goods'

Vue.use(Vuex)

Vue.config.debug = false

// 
const state = {
	// TODO: 
	GOODS: GET_GOODS(0)
}

//  mutation 
const mutations = {
	// TODO: 
	PAGE_GOODS (state, page) {
		state.GOODS = GET_GOODS(page)
	}
}

//  store
//  store 
export default new Vuex.Store({
	state,
	mutations
})


